/// Grid
%padding {
    padding-left: 1rem;
    padding-right: 1rem;
    box-sizing: border-box; }

.container {
    width: 1200px;
    @extend %padding; }


.container-fluid {
    width: 100%;
    @extend %padding; }

.row {
    width: 100%;
    box-sizing: border-box; }

@for $i from 1 through $columns {
    .col-#{$i} {
        min-height: 1px;
        box-sizing: border-box;
        background-color: mediumspringgreen;

        @each $key, $value in $grid-breakpoints {
            @include respond-to($key) {
                @if $value <= 480px {
                    width: 100%; }
                @else if $value <= 768px {
                    width: 50%; }
                @else {
                    width: 100% * $i / $columns; } } } } }
